import React, { HTMLAttributes, ReactNode } from 'react';
import classNames from 'classnames';

import { Container } from '../Container';
import { Stack } from '../Stack';
import { Heading, Text } from '../Typography';

import styles from './Hero.module.scss';

export interface HeroProps
    extends Omit<HTMLAttributes<HTMLElement>, 'className'> {
    image: ReactNode | string;
    heading: string;
    description: string;
    children?: ReactNode;
    className?: string;
}

export const Hero: React.FC<HeroProps> = ({
    image,
    heading,
    description,
    children,
    ...props
}) => {
    // Remove unnecessary props
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const { className, ...sectionProps } = props as HTMLAttributes<HTMLElement>;

    const imageIsString = typeof image === 'string';

    return (
        <section
            className={styles.base}
            style={{ backgroundImage: `url(${imageIsString ? image : ''})` }}
            {...sectionProps}
        >
            {!imageIsString && image}
            <div className={styles.mask}>
                <Container className={styles.container}>
                    <div className={classNames(styles.inner, className)}>
                        <Stack gap={{ xsmall: 2 }}>
                            <Heading variant="h1" className={styles.title}>
                                {heading}
                            </Heading>
                            <Text className={styles.description}>
                                {description}
                            </Text>
                            {children}
                        </Stack>
                    </div>
                </Container>
            </div>
        </section>
    );
};

export default Hero;
